.staff-button__box {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2; }
  .staff-button__box .staff-button-next,
  .staff-button__box .staff-button-prev {
    top: 0;
    left: 0;
    outline: none;
    position: relative;
    z-index: 1;
    cursor: pointer; }
    .staff-button__box .staff-button-next::after,
    .staff-button__box .staff-button-prev::after {
      position: absolute;
      font-family: "fontello";
      z-index: 2;
      top: 0;
      left: 1.5rem;
      content: "";
      font-size: 2.4rem;
      width: 5px;
      height: 10px; }
  .staff-button__box .staff-button-prev::after {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    content: ""; }
  .staff-button__box .staff-button-next::after {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    transform: rotate(180deg) translate(-135%, -190%);
    content: ""; }
  .staff-button__box .staff-button-next {
    margin-right: 0.86rem; }

.staff-swiper__slide {
  border-right: none;
  display: table; }
  @media (min-width: 768px) {
    .staff-swiper__slide {
      position: relative; }
      .staff-swiper__slide::before {
        content: "";
        position: absolute;
        border-right: 1px solid rgba(113, 113, 126, 0.5);
        top: 0;
        left: 100%;
        transform: rotate(180deg) translate(0%, -7rem);
        height: 12.2rem; } }

.questions {
  margin-top: 5rem; }
  .questions input[type="checkbox"] {
    display: none; }
    .questions input[type="checkbox"]::before {
      display: none; }
  .questions input[type="checkbox"] + label::before,
  .questions input:checked + label::after {
    display: none; }
  .questions .accordion {
    margin: 0 auto;
    width: 100%;
    max-width: 95.3rem;
    /* При нажатии кнопки мыши на метке, чекбокс становится отмеченным и соответствующая метка получает стиль “выделенной” */
    /* Добавляем трансформацию для высоты и тени. Трансформация будет работать при "закрывании" пункта  */
    /* стиль для содержания */ }
    .questions .accordion__box {
      margin-bottom: 2rem; }
    .questions .accordion label {
      padding: 2.5rem 4.5rem 2.5rem 2rem;
      position: relative;
      z-index: 2;
      display: block;
      height: auto;
      cursor: pointer;
      color: gray;
      background: white;
      border: 1px solid gray;
      box-sizing: border-box;
      border-radius: 5px;
      transition: all 0.4s;
      font-style: normal;
      font-weight: bold;
      font-size: 1.4rem;
      line-height: 140%;
      font-feature-settings: "pnum" on, "lnum" on; }
      @media (min-width: 768px) {
        .questions .accordion label {
          padding: 2.5rem 9rem 2.5rem 2rem; } }
      .questions .accordion label:hover, .questions .accordion label:focus {
        background: blue;
        color: white;
        transition: all 0.4s; }
    .questions .accordion input:checked + label,
    .questions .accordion input:checked + label:hover {
      background: blue;
      color: white;
      transition: all 0.4s;
      border-radius: 5px 5px 0px 0px; }
    .questions .accordion article {
      background: rgba(255, 255, 255, 0.5);
      border-radius: 0px 0px 5px 5px;
      overflow-y: auto;
      height: 0px;
      position: relative;
      z-index: 2;
      transition: height 0.3s ease-in-out, box-shadow 0.6s linear;
      padding-right: 1.7rem; }
      .questions .accordion article::-webkit-scrollbar {
        background-color: rgba(255, 255, 255, 0.5);
        /* цвет скролбара под цвет тела, чтоб было не видно */ }
      .questions .accordion article::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0); }
      .questions .accordion article::-webkit-scrollbar-thumb {
        background-color: rgba(255, 255, 255, 0.5);
        /* цвет плашки */ }
      .questions .accordion article::-webkit-scrollbar-track-piece {
        background-color: transparent;
        -webkit-border-radius: 6px; }
    .questions .accordion input:checked ~ article {
      transition: height 0.5s ease-in-out, box-shadow 0.1s linear;
      height: 21rem;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 0px 0px 5px 5px; }
    .questions .accordion article p {
      padding: 2.7rem 3.1rem 4.1rem 3.1rem;
      font-style: normal;
      font-weight: normal;
      font-size: 1.4rem;
      line-height: 160%;
      font-feature-settings: "pnum" on, "lnum" on;
      color: gray; }
      @media (min-width: 1320px) {
        .questions .accordion article p {
          font-size: 1.8rem; } }
    .questions .accordion i {
      position: absolute;
      right: 11%;
      top: 45%; }
    .questions .accordion #ac-1:checked + label i::before,
    .questions .accordion #ac-2:checked + label i::before,
    .questions .accordion #ac-3:checked + label i::before,
    .questions .accordion #ac-4:checked + label i::before,
    .questions .accordion #ac-5:checked + label i::before {
      transform: translate(-5px, 8px) rotate(45deg);
      background-color: white;
      transition: transform 0.5s ease; }
    .questions .accordion #ac-1:checked + label i::after,
    .questions .accordion #ac-2:checked + label i::after,
    .questions .accordion #ac-3:checked + label i::after,
    .questions .accordion #ac-4:checked + label i::after,
    .questions .accordion #ac-5:checked + label i::after {
      transform: translate(5px, 8px) rotate(-45deg);
      background-color: white;
      transition: transform 0.5s ease; }
    .questions .accordion i::after {
      position: absolute;
      content: "";
      width: 16px;
      height: 3px;
      background-color: blue;
      transition: transform 0.5s ease;
      transform: translate(-5px, 8px) rotate(-45deg); }
    .questions .accordion i::before {
      position: absolute;
      content: "";
      width: 16px;
      height: 3px;
      background-color: blue;
      transition: transform 0.5s ease;
      transform: translate(5px, 8px) rotate(45deg); }
    .questions .accordion label:focus i::before,
    .questions .accordion label:hover i::before,
    .questions .accordion label:focus i::after,
    .questions .accordion label:hover i::after {
      background-color: white;
      transition: transform 0.5s ease; }
